* {
  box-sizing: border-box;
}

html {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
  font-weight: 700;

  color: rgb(47, 79, 79);;
}

body {
  margin: 0;
}

.people {
  --gridColumnCount: 2;

  .container {
    padding: 30px 35px;
    display: grid;
    grid-template-columns: repeat(var(--gridColumnCount), 200px);
    place-items: center;
    place-content: center;
    gap: 40px 30px;

    @media (min-width: 1000px) {
      --gridColumnCount: 4;
    }
  }
}

.person {
  transition: transform 300ms linear 1000ms;
  transition: border-radius 300ms linear 0;
  transition: background-color 300ms linear 0;

  padding: 10px;


  .photo {
    position: relative;
  }

  .image {
    display: block;
    width: 200px;
    height: 200px;
    margin-bottom: 8px;
    border-radius: 100%;

    transition: border-radius 300ms linear 1000ms;
  }

  .rank {
    position: absolute;
    top: (200px / 2) - (32px / 2);
    right: 10px;
    
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    width: 32px;
    height: 32px;
    border-radius: 100%;
  
    color: rgb(139, 0, 0);;
  }

  .details {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 9px;
  }

  .extra {
    font-size: 18px;
    font-weight: 400;

    color: #000;

    opacity: 0;
  }

  &:hover {
    border-radius: 10px;

    background-color: rgb(238, 238, 238);

    transform: scale(1.2);

    .person {
      .extra {
        opacity: 1;
      }
      .image {
        border-radius: 60px;
      }
    }
  } 
}